import React, { useState } from 'react';
import { Box, Typography, Grid, Paper } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import ImageModal from '../components/ImageModal';

const Visualizations: React.FC = () => {
  const theme = useTheme();
  const [selectedImage, setSelectedImage] = useState<{src: string, alt: string} | null>(null);

  const visualizations = [
    {
      title: 'Age Distribution',
      description: 'Distribution of crab ages in the dataset, showing the frequency of different age groups. This visualization helps us understand the overall age structure of the crab population in our study.',
      image: 'age_distribution.png'
    },
    {
      title: 'Age by Sex',
      description: 'Comparison of age distributions across different crab sexes. This analysis reveals potential differences in aging patterns between male and female crabs.',
      image: 'age_by_sex.png'
    },
    {
      title: 'Feature Importance',
      description: 'Relative importance of different physical measurements in predicting crab age. This helps identify which characteristics are most reliable for age estimation.',
      image: 'feature_importance.png'
    },
    {
      title: 'Correlation Matrix',
      description: 'Heatmap showing correlations between different physical measurements. Understanding these relationships helps us identify which features might be redundant or complementary.',
      image: 'correlation_matrix.png'
    },
    {
      title: 'Length vs Age by Sex',
      description: 'Relationship between crab length and age, separated by sex. This visualization explores how crab length changes with age and whether this relationship differs between males and females.',
      image: 'scatter_Length_vs_age_by_sex.png'
    },
    {
      title: 'Weight vs Age by Sex',
      description: 'Relationship between crab weight and age, separated by sex. This analysis shows how crab weight correlates with age and reveals any sex-based differences in this relationship.',
      image: 'scatter_Weight_vs_age_by_sex.png'
    }
  ];

  const handleImageClick = (imagePath: string, imageAlt: string) => {
    setSelectedImage({
      src: `${process.env.PUBLIC_URL}/output/plots/${imagePath}`,
      alt: imageAlt
    });
  };

  return (
    <Box>
      <Typography
        variant="h1"
        sx={{
          mb: 4,
          textAlign: 'center',
          fontSize: { xs: '2rem', sm: '2.5rem', md: '3rem' }
        }}
      >
        Data Visualizations
      </Typography>

      <Typography
        variant="h5"
        sx={{
          mb: 4,
          textAlign: 'center',
          color: theme.palette.text.secondary,
          maxWidth: '800px',
          mx: 'auto'
        }}
      >
        Explore our comprehensive set of visualizations that reveal patterns and relationships in our crab age prediction study. Click on any visualization to view it in detail.
      </Typography>

      <Grid container spacing={4}>
        {visualizations.map((viz, index) => (
          <Grid item xs={12} md={6} key={viz.title}>
            <Paper
              sx={{
                p: 3,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                borderTop: `4px solid ${
                  index % 3 === 0
                    ? theme.palette.primary.main
                    : index % 3 === 1
                    ? theme.palette.secondary.main
                    : theme.palette.warning.main
                }`,
                transition: 'transform 0.2s, box-shadow 0.2s',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: theme.shadows[4],
                  cursor: 'pointer'
                }
              }}
              onClick={() => handleImageClick(viz.image, viz.title)}
            >
              <Typography
                variant="h4"
                sx={{
                  mb: 2,
                  color:
                    index % 3 === 0
                      ? theme.palette.primary.main
                      : index % 3 === 1
                      ? theme.palette.secondary.main
                      : theme.palette.warning.main
                }}
              >
                {viz.title}
              </Typography>
              <Typography
                variant="body1"
                sx={{
                  mb: 2,
                  color: theme.palette.text.secondary
                }}
              >
                {viz.description}
              </Typography>
              <Box
                sx={{
                  mt: 'auto',
                  position: 'relative',
                  width: '100%',
                  pt: '75%', // 4:3 aspect ratio
                  overflow: 'hidden',
                  borderRadius: 1
                }}
              >
                <Box
                  component="img"
                  src={`${process.env.PUBLIC_URL}/output/plots/${viz.image}`}
                  alt={viz.title}
                  sx={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%',
                    objectFit: 'contain'
                  }}
                />
              </Box>
            </Paper>
          </Grid>
        ))}
      </Grid>

      {selectedImage && (
        <ImageModal
          open={Boolean(selectedImage)}
          onClose={() => setSelectedImage(null)}
          imageSrc={selectedImage.src}
          imageAlt={selectedImage.alt}
        />
      )}
    </Box>
  );
};

export default Visualizations;